import { Watchlist } from "@rp/common/api-types"
import { Box } from "@rp/react/web/mui/core"
import { Edit } from "@rp/react/web/mui/icons"

type Props = {
  url: string
  canEdit: boolean
  watchlist: Watchlist
  rename: {
    onClick: (watchlist: Watchlist, autoFocus: "imageUrl") => void
  }
}

export const WatchlistImage = ({ url, canEdit, rename, watchlist }: Props) => {
  return (
    <Box
      onClick={() => canEdit && rename.onClick(watchlist, "imageUrl")}
      sx={{
        backgroundImage: `url(${url})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        height: "124px",
        width: "124px",
        borderRadius: "12px",
        flexShrink: 0,
        position: "relative",
        cursor: "pointer",
        "&:hover": {
          "& > div": {
            opacity: 1,
          },
        },
      }}
    >
      {canEdit && (
        <Box
          sx={{
            opacity: 0,
            position: "absolute",
            width: "100%",
            height: "100%",
            backgroundColor: "rgba(0, 0, 0, 0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            borderRadius: 2,
          }}
        >
          <Edit />
        </Box>
      )}
    </Box>
  )
}
